import React, { CSSProperties, FC, useRef, useState } from "react";
import clsx from "clsx";

import {
  FullScreen,
  FullScreenRefValues,
  GetCoinImg,
  SwitchBox,
} from "components";
import { useWindowSize } from "providers";
import { useResizeObserver } from "hooks";

import { Buttons, Hint } from "./components";
import css from "./style.module.scss";

interface Props {
  url?: string;
  isRealPlay?: boolean;
  changeFreePlay: (value: boolean) => void;
  maxProfit?: string;
  favorites: number;
  gameId?: string;

  isLiked: boolean;
  setIsLiked: (value: boolean) => void;
  setFavorites: (value: number) => void;
}

const SlotGame: FC<Props> = ({
  url,
  changeFreePlay,
  isRealPlay,
  maxProfit,
  favorites,
  gameId,
  isLiked,
  setIsLiked,
  setFavorites,
}) => {
  const sizes = useWindowSize();
  const [rootHeight, setRootHeight] = useState<CSSProperties>({});
  const [rootNode, setRootNode] = useState<HTMLDivElement | null>(null);
  const [isOnFullScreen, setIsOnFullScreen] = useState(false);

  const fullScreenRef = useRef<FullScreenRefValues | null>(null);

  useResizeObserver(
    (entries) => {
      const { width: elementWidth } = entries[0].contentRect;
      const { offsetWidth: documentWidth, offsetHeight: documentHeight } =
        document.body;

      if (documentWidth >= 600) {
        const maxHeight = (documentHeight - 64) * 0.8; // 80% from (height - header)
        const calcHeight = elementWidth / 1.77;

        setRootHeight({
          height: `${calcHeight < maxHeight ? calcHeight : maxHeight}px`,
        });
      } else {
        setRootHeight({
          height: `${documentHeight - (60 + 90 + 56 + 100)}px`, //game buttons + bottom menu + header + gameHeader
        });
      }
    },
    rootNode,
    [url, rootNode, sizes],
  );

  return (
    <div className={css.container}>
      <div className={clsx(css.topWrapper, css.frameLine)}>
        <span className={clsx(css.switchText, css.playMode)}>
          {!!isRealPlay ? (
            <>
              {"Real Play"}
              <GetCoinImg className={css.playCoin} />
            </>
          ) : (
            "Free Play"
          )}
        </span>
        <SwitchBox
          text={{
            off: "Free Play",
            on: "Real Play",
          }}
          isChecked={!!isRealPlay}
          switchHandleChange={changeFreePlay}
          classes={{
            root: css.switchRoot,
            text: css.switchText,
            switch: {
              toggle: css.toggle,
              track: css.track,
            },
          }}
        />
      </div>
      <FullScreen ref={fullScreenRef} warnAboutIsFullScreen={setIsOnFullScreen}>
        <div
          className={css.root}
          ref={(node) => setRootNode(node)}
          style={isOnFullScreen ? {} : rootHeight}>
          {url ? (
            <iframe
              src={url}
              frameBorder="0"
              className={css.frame}
              allowFullScreen={true}
              title="game"
            />
          ) : (
            <Hint changeFreePlay={changeFreePlay} />
          )}
        </div>
      </FullScreen>
      <Buttons
        className={clsx(css.bottomWrapper, css.frameLine)}
        fullscreenChange={() => {
          fullScreenRef.current?.enterFullscreen();
        }}
        likes={favorites}
        setFavorites={setFavorites}
        maxProfit={maxProfit}
        gameId={gameId}
        isLiked={isLiked}
        setIsLiked={setIsLiked}
      />
    </div>
  );
};

export default SlotGame;
