import { Button } from "components";
import React, { FC } from "react";

import { ReactComponent as PlayIcon } from "./playIcon.svg";
import css from "./style.module.scss";

interface Props {
  changeFreePlay: (value: boolean) => void;
}

export const Hint: FC<Props> = ({ changeFreePlay }) => {
  return (
    <div className={css.root}>
      <p className={css.text}>
        Entering a third-party game. The balance of the selected currency will
        be displayed in USD, and if you change currency while playing, the game
        will refresh and restart.
      </p>
      <div className={css.buttonWrapper}>
        <Button className={css.button} onClick={() => changeFreePlay(true)}>
          <PlayIcon />
          Real Play
        </Button>
        <Button className={css.button} onClick={() => changeFreePlay(false)}>
          <PlayIcon />
          Free Play
        </Button>
      </div>
    </div>
  );
};
