//@ts-nocheck

import React, {
  DetailedHTMLProps,
  FC,
  HTMLAttributes,
  useEffect,
  useRef,
  useState,
} from "react";
import clsx from "clsx";

import { useBalanceAnimation, useUser } from "providers";
import { Button, chatPipeFunctions, LiveStats, Popper } from "components";
import { useEventListener } from "hooks";

import { liveStatsManager } from "components/LiveStats/eventEmitter/emitter";

import { ReactComponent as FullScreen } from "assets/gamePage/fullScreen.svg";
import { ReactComponent as Graph } from "assets/gamePage/graph.svg";
import { ReactComponent as Quest } from "assets/gamePage/quest.svg";
import { ReactComponent as FillStar } from "./assets/star.svg";
import css from "./style.module.scss";
import { fetchBase } from "utils";

interface Props
  extends DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement> {
  fullscreenChange: () => void;
  maxProfit?: string;
  likes: number;
  gameId?: string;

  isLiked: boolean;
  setIsLiked: (value: boolean) => void;
  setFavorites: (value: number) => void;
}

export const Buttons: FC<Props> = ({
  className,
  fullscreenChange,
  maxProfit,
  likes,
  setFavorites,

  gameId,

  isLiked,
  setIsLiked,
  ...props
}) => {
  const { balanceDispatch } = useBalanceAnimation();
  const { user } = useUser();

  const questionRef = useRef<HTMLButtonElement>(null);
  const popperRef = useRef<HTMLDivElement>(null);
  const [isLiveStats, setIsLiveStats] = useState(false);
  const [isPopperOpen, setIsPopperOpen] = useState(false);
  const buttons = [
    {
      icon: Quest,
      callBack: () => setIsPopperOpen(!isPopperOpen),
      isRef: true,
    },
    {
      icon: Graph,
      callBack: () => setIsLiveStats(!isLiveStats),
    },
    {
      icon: FullScreen,
      callBack: fullscreenChange,
    },
  ];

  useEventListener("click", ({ target }) => {
    if (
      target !== questionRef.current &&
      target !== popperRef.current &&
      !popperRef.current?.contains(target as HTMLElement)
    ) {
      setIsPopperOpen(false);
    }
  });

  useEffect(() => {
    const slotListener = (data: SlotListener) => {
      if (data.user_name === user?.user_name) {
        liveStatsManager.addBet({
          amount: Number(data.bet_amt),
          profit: Number(data.profit),
          coin: data.coin,
        });

        if (Number(data.profit) > 0) {
          balanceDispatch({
            type: "update",
            value: {
              value: `+${data.profit}`,
              id: Math.random().toString(),
            },
          });
        }
      }
    };

    chatPipeFunctions.slotPageListener = slotListener;

    return () => {
      chatPipeFunctions.slotPageListener = () => null;
    };
  }, [user]);

  const like = async () => {
    setIsLiked(true);
    setFavorites(likes + 1);

    await fetchBase<{ success: boolean }>("slots/like", {
      variables: {
        game_id: gameId,
      },
    });
  };
  const unLike = async () => {
    setIsLiked(false);
    setFavorites(Math.max(likes - 1, 0));

    await fetchBase<{ success: boolean }>("slots/unlike", {
      variables: {
        game_id: gameId,
      },
    });
  };

  return (
    <div {...props} className={clsx(css.root, className)}>
      <LiveStats
        isOpen={isLiveStats}
        handleClose={() => setIsLiveStats(false)}
      />
      <Popper
        ref={popperRef}
        backgroundColor="rgba(0, 0, 0, 0.95)"
        anchor={questionRef.current}
        isOpen={isPopperOpen}
        position="bottom"
        className={css.popper}>
        {maxProfit ? `Max Profit: ${maxProfit}$` : "Game session not started"}
      </Popper>

      <div className={css.content}>
        <div
          className={css.likesWrapper}
          onClick={() => {
            isLiked ? unLike() : like();
          }}>
          <FillStar
            className={clsx(css.star, !isLiked ? css.fillStar : css.strokeStar)}
          />
          <span className={css.favorites}>{likes} Favorites</span>
        </div>

        <div className={css.buttonWrapper}>
          {buttons.map(({ callBack, icon: Icon, isRef }) => (
            <Button
              className={css.button}
              onClick={callBack}
              ref={isRef ? questionRef : null}>
              <Icon />
            </Button>
          ))}
        </div>
      </div>
    </div>
  );
};
